#nrs_news_post {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#nrs_news_post article {
  position: relative;
  background-color: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
}

#nrs_news_post article:not(:last-child) {
  margin-bottom: 20px;
}

#nrs_news_post article .news-post-title {
  font-family: "Open Sans";
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.2em;
}

#nrs_news_post article .news-post-title h1 {
  color: #333333;
}

#nrs_news_post article .news-post-featured-image {
  text-align: center;
}

#nrs_news_post article .news-post-featured-image img {
  vertical-align: middle;
  display: inline-block;
  box-shadow: none;
  border-radius: 0;
  height: auto;
  max-width: 100%;
  border: none;
  max-width: 235px;
}

#nrs_news_post article .news-post-content {
  color: #333333;
  font-size: 18px;
  line-height: 1.5em;
  font-weight: 400;
}

#nrs_news_post article .news-post-navigation {
  display: flex;
}

#nrs_news_post article .news-post-navigation nav {
  width: 100%;
}

#nrs_news_post article .news-post-navigation h2.screen-reader-text {
  display: none;
}

#nrs_news_post article .news-post-navigation .nav-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#nrs_news_post article .news-post-navigation .nav-links > div {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

#nrs_news_post article .news-post-navigation .nav-links > div.nav-next {
  display: flex;
  justify-content: flex-end;
}

#nrs_news_post article .news-post-navigation .nav-links > div a {
  display: flex;
  align-items: center;
  max-width: 100%;
  box-shadow: none;
  text-decoration: none;
}

#nrs_news_post article .news-post-navigation .nav-links > div a span {
  color: #004378;
  font-size: 16px;
  font-weight: 600;
}

#nrs_news_post article .news-post-navigation .nav-links > div a span.meta-nav {
  margin: 0 10px;
}

.news-post-container a {
  box-shadow: none;
  text-decoration: none;
}

.news-post-search {
  margin: 10px 0 2em;
}

.news-post-search form div {
  display: flex;
  justify-content: flex-end;
  border-color: #cccccc;
  border-width: 1px 1px 1px 1px;
  border-radius: 3px;
}

.news-post-search form div label:first-child {
  opacity: 0;
}

.news-post-search form div input[type="text"] {
  -webkit-appearance: none;
  background-color: #ffffff;
  border-color: #cccccc;
  border-width: 1px 1px 1px 1px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  color: #999999;
  display: inline-block;
  font-size: 20px;
  line-height: 1;
  margin: 0;
  max-width: 80%;
  min-width: 0;
  padding: 10px calc(50px / 3);
  transition: color .2s;
  vertical-align: middle;
  white-space: normal;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
}

.news-post-search form div input[type="submit"] {
  color: #a6a6a6;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
}

@media (min-width: 300px) {
  #nrs_news_post article {
    padding: 2% 5% 3% 5%;
    width: auto;
    margin-top: 2em;
  }
}

@media (min-width: 768px) {
  #nrs_news_post article {
    padding: 2% 3% 3% 3%;
  }
}
