#n_albert {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-bottom: 7rem;
}

#n_albert .n_jonathan {
  border: 3px dashed green;
}

#n_albert .n_joan {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  box-shadow: inset 1px 1px 6px 1px #ccc;
}

#n_albert .n_mildred {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#n_albert .n_katherine {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#n_albert .n_katherine input {
  padding: 1rem 0.5rem;
  margin: 1rem 0;
  font-size: 2rem;
  text-align: center;
  border-radius: 5px;
}

@media (min-width: 300px) {
  #n_albert .n_joan {
    max-width: 30rem;
    width: 100%;
    padding: 2rem 0 2rem;
    background-color: #fff;
  }
  #n_albert .n_justin {
    display: flex;
    width: 100%;
    margin-top: 1rem;
  }
  #n_albert .n_justin a {
    border: 1px solid green;
    width: 100%;
    padding: 1rem;
    text-align: center;
    background-color: #203864;
    color: #fff;
    padding: 1.5rem;
    border-radius: 5px;
  }
}
