body.modal-open {
  overflow: hidden;
}

#deb .photograph img, #shane .photograph img, #alberto .photograph img, #lance .photograph img, #justin .photograph img {
  transform: translateY(-3px);
}

#marc .photograph img {
  transform: translateY(4px);
}

#marc .modal img {
  height: 17rem;
  left: -1rem;
}

#deb .modal img, #shane .modal img, #alberto .modal img, #lance .modal img, #justin .modal img {
  height: 14rem;
  left: 0rem;
}

#patrick .modal img {
  height: 16rem;
  left: 0rem;
}

#elizabeth .modal img {
  height: 16rem;
  left: 0rem;
}

#al img {
  transform: translateY(-11px) scale(0.87);
}

@media screen and (min-width: 300px) {
  .leadership-container {
    color: #000;
    line-height: inherit;
    font-size: medium;
    font-size: initial;
    line-height: initial;
  }
  .main-container {
    align-items: stretch;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    margin-top: 1em;
    padding-bottom: 1em;
    background-color: #fff;
    box-shadow: inset 1px 1px 6px 1px #ccc;
  }
  .team-header {
    color: #999;
    display: flex;
    font-size: 1.3em;
    font-variant: small-caps;
    font-weight: bold;
    justify-content: center;
    padding: 1em;
  }
  .team-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 100%;
  }
  .teammember {
    margin: 0 0.2em;
    padding: 0 0.5em .5em 0.5em;
    cursor: pointer;
    width: 11em;
    min-width: 11em;
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
  }
  .teammember:hover {
    background-color: #e9f3fb;
  }
  .teammember .photograph {
    border-radius: 50%;
    width: 10em;
    height: 10em;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    margin: 1em 0;
    cursor: pointer;
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
  }
  .teammember .photograph .arrowsign {
    box-sizing: content-box;
    position: absolute;
    background-color: white;
    right: 38px;
    bottom: .5em;
    z-index: 1;
    border-radius: 50%;
    border: 1px solid #5ba2e2;
    padding: .6em;
    opacity: 1;
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
    width: 1em;
    height: 1em;
  }
  .teammember .photograph .arrowsign:after {
    box-sizing: content-box;
    content: '';
    display: inline-block;
    width: 0.5em;
    height: 0.5em;
    border-top: 0.2em solid #5ba2e2;
    border-right: 0.2em solid #5ba2e2;
    transform: rotate(45deg);
    transform-origin: 87% 50%;
  }
  .teammember .photograph img {
    display: inline;
    height: 100%;
    width: auto;
    height: 11em;
    transform: translateY(20px) scale(1.2);
    transition-property: all;
    transition-duration: 0.3s;
    transition-timing-function: ease-in-out;
  }
  .teammember .caption {
    width: 95%;
  }
  .teammember .caption .fullname {
    font-weight: bold;
    font-size: 1em;
  }
  .teammember .caption .jobtitle {
    font-size: .8em;
    color: #808080;
  }
  .teammember .modal {
    background-color: black;
    /* Fallback color */
    background-color: rgba(15, 98, 168, 0.4);
    /* Black w/ opacity */
    display: none;
    /* Hidden by default */
    height: 100%;
    /* Full height */
    left: 0;
    margin-left: 0;
    overflow: auto;
    /* Enable scroll if needed */
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    /* Stay in place */
    top: 0;
    width: 100%;
    /* Full width */
    z-index: 100;
  }
  .teammember .modal.active {
    display: flex;
  }
  .teammember .modal .modal-content {
    background-color: #fefefe;
    max-width: 900px;
    width: 89%;
    position: relative;
  }
  .teammember .modal .closemodal {
    float: right;
    font-weight: bold;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    background-color: #5ba2e2;
    box-shadow: 1px 0px 2px #333;
  }
  .teammember .modal .closemodal {
    position: absolute;
    right: -15px;
    top: -15px;
    width: 36px;
    height: 36px;
    z-index: 1;
  }
  .teammember .modal .closemodal:before, .teammember .modal .closemodal:after {
    position: absolute;
    left: 16px;
    top: 8px;
    content: ' ';
    height: 20px;
    width: 5px;
    background-color: #fff;
  }
  .teammember .modal .closemodal:before {
    transform: rotate(45deg);
  }
  .teammember .modal .closemodal:after {
    transform: rotate(-45deg);
  }
  .teammember .modal .closemodal:hover,
  .teammember .modal .closemodal:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  .teammember .modal .modalwrapper {
    display: flex;
    flex-direction: row;
    height: 90vh;
    position: relative;
  }
  .teammember .modal .pic {
    position: absolute;
    top: 20px;
    left: 10px;
    border-radius: 50%;
    overflow: hidden;
    width: 120px;
    height: 120px;
    background-image: none;
  }
  .teammember .modal .pic img {
    max-width: 180px;
    padding-top: 20%;
    height: 12em;
    margin: 0 auto;
    padding-top: 0px;
    position: absolute;
    right: -13px;
    top: 0px;
    width: auto;
  }
  .teammember .modal .desc {
    padding: 2rem;
    flex: auto;
    width: 100%;
  }
  .teammember .modal .desc-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  .teammember .modal .modal-name-wrapper {
    display: flex;
    flex-direction: column;
    height: 14rem;
    justify-content: center;
    margin-bottom: 20px;
    margin-left: 7em;
  }
  .teammember .modal .modal-member-name {
    font-weight: bold;
    font-size: 2rem;
    line-height: 2.5rem;
    margin-bottom: 1rem;
  }
  .teammember .modal .modal-member-title {
    color: #666;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
  .teammember .modal .modal-bio-wrapper {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    height: 68%;
  }
  .teammember .modal .modal-bio-wrapper p {
    margin-top: 0;
    font-size: 1.7rem;
    line-height: 2.7506rem;
    color: #666;
  }
  .teammember .modal .modal-bio-wrapper p br {
    line-height: 5rem;
  }
  .teammember .modal .modal-bio-wrapper p a {
    overflow-wrap: break-word;
    word-break: break-word;
    color: #94be6f;
  }
  .teammember .modal .modal-bio-wrapper p a:hover {
    color: #659a44;
    text-decoration: none;
  }
}

@media screen and (min-height: 568px) {
  .teammember .modal .modal-bio-wrapper {
    height: 68%;
  }
}

@media screen and (min-width: 600px) {
  .teammember .modal .modal-content {
    width: 90%;
  }
  .teammember .modal .modalwrapper {
    height: 500px;
    position: static;
  }
  .teammember .modal .pic {
    position: static;
    background: linear-gradient(to bottom, #5ba2e2, #054877);
    border-radius: inherit;
    width: auto;
    height: auto;
  }
  .teammember .modal .pic img {
    position: static;
    max-width: 180px;
    padding-top: 20%;
    height: auto !important;
  }
  .teammember .modal .desc {
    width: 50%;
    padding: 2rem 5%;
  }
  .teammember .modal .modal-name-wrapper {
    height: 20%;
    margin: 0;
  }
  .teammember .modal .modal-member-name {
    font-size: 2.5rem;
    line-height: 4.045rem;
    margin-bottom: 0;
  }
  .teammember .modal .modal-member-title {
    font-size: 1.1em;
    line-height: 2.2rem;
  }
  .teammember .modal .modal-bio-wrapper {
    height: 75%;
  }
}

@media screen and (min-height: 640px) {
  .teammember .modal .modal-bio-wrapper {
    height: 75%;
  }
}

@media screen and (min-height: 812px) {
  .teammember .modal .modal-bio-wrapper {
    height: 79%;
  }
}
