.abcde {
  color: red;
}

#header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#header-container .lsm-container {
  display: flex;
  flex-direction: row;
}

#header-container .lsm-container > div {
  display: flex;
  align-items: center;
}

#header-container .lsm-container .portal-link a {
  color: #fff;
  background: url(../../images/splitter.gif) no-repeat 0 25px;
  background-position: right center;
  margin-right: 1.1rem;
  padding-right: 1.3rem;
}

#header-container .lsm-container .menu {
  list-style-type: none;
  margin-left: 0;
  padding-left: 0;
}

#header-container .lsm-container .menu li a, #header-container .lsm-container .menu li span {
  color: #fff;
}

#header-container .lsm-container .menu > li:last-child a {
  display: flex;
  align-items: baseline;
}

@media (min-width: 300px) {
  #header-container {
    padding: 0 3%;
  }
  #header-container .logo-wrapper {
    display: none;
  }
  #header-container #page-links-nav-container {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
    background-image: linear-gradient(to top, #054877, #2f3256, #2d2035, #1e131a, #000000);
  }
  #header-container #page-links-nav-container ul, #header-container #page-links-nav-container li {
    list-style-type: none;
  }
  #header-container #page-links-nav-container a {
    color: #fff;
  }
  #header-container #page-links-nav-container .closebtn {
    position: absolute;
    top: 3rem;
    right: 2rem;
    font-size: 5rem;
    margin-left: 5rem;
  }
  #header-container #page-links-nav-container .menu li {
    padding: 1rem 0;
  }
  #header-container #x_open {
    background-color: #E01E26;
    color: #fff;
    border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.1), 0 1px 0 rgba(255, 255, 255, 0.075);
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
    border-radius: .3em;
    font-size: 2.3rem;
    margin: 1rem 0 1rem 1rem;
  }
}

@media (min-width: 980px) {
  #header-container {
    padding: 0 3% 0 1%;
  }
  #header-container .logo-wrapper {
    display: flex;
    flex-direction: row;
    width: 290px;
    margin-left: 3%;
  }
  #header-container .logo-wrapper .logo-self {
    background-image: url(../../images/nrs_logo.png);
    background-repeat: no-repeat;
    margin-top: .5em;
    margin-bottom: .5em;
    background-size: contain;
    padding: 30px 103px;
    z-index: 1;
  }
  #header-container .logo-wrapper .logo-decor {
    display: none;
    background-image: url(../../images/world_map.png);
    background-repeat: no-repeat;
    margin-left: -9em;
    margin-top: .5em;
    margin-bottom: .5em;
    background-size: contain;
    padding: 30px 76px;
  }
  #header-container #abc {
    color: #fff;
  }
  #header-container #page-links-nav-container {
    width: auto;
    display: flex;
    justify-content: space-between;
    height: auto;
    position: inherit;
    padding-top: 0;
    overflow: inherit;
    background-image: none;
    background-color: #203864;
    align-items: center;
  }
  #header-container #page-links-nav-container > div {
    display: flex;
    justify-content: center;
  }
  #header-container #page-links-nav-container .closebtn {
    display: none;
  }
  #header-container #page-links-nav-container .menu {
    display: flex;
    justify-content: space-around;
  }
  #header-container #page-links-nav-container .menu .current-menu-item, #header-container #page-links-nav-container .menu .active {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #header-container #page-links-nav-container .menu .current-menu-item:after, #header-container #page-links-nav-container .menu .active:after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid red;
    margin-top: -0.6rem;
  }
  #header-container #page-links-nav-container .menu > li {
    transition: all 0.2s ease;
  }
  #header-container #page-links-nav-container .menu > li:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
  #header-container #page-links-nav-container .menu > li:hover > a {
    text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
  }
  #header-container #page-links-nav-container ul {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding-left: 0;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }
  #header-container #page-links-nav-container li {
    display: flex;
    list-style: none;
    flex: auto;
    text-align: center;
    position: relative;
  }
  #header-container #page-links-nav-container li a {
    white-space: nowrap;
    display: flex;
    flex: auto;
    justify-content: center;
    text-decoration: none;
    padding: 0.5rem;
    margin: 0 .5rem;
    color: #fff;
    align-items: baseline;
  }
  #header-container #page-links-nav-container li a:visited {
    text-decoration: none;
  }
  #header-container #page-links-nav-container li a:hover {
    text-decoration: none;
    color: #fff;
  }
  #header-container #page-links-nav-container li a:active {
    text-decoration: none;
  }
  #header-container #page-links-nav-container li ul.sub-menu {
    flex-direction: column;
    display: none;
  }
  #header-container #page-links-nav-container li ul.sub-menu::before {
    content: "";
    position: absolute;
    top: -10px;
    left: 2.5rem;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 10px solid #fff;
  }
  #header-container #page-links-nav-container li ul.sub-menu.showing {
    border: 1px solid #e7e7e7;
    border-radius: 0.5rem;
    display: flex;
    position: absolute;
    top: 3.5rem;
    left: 0;
    background-color: white;
    width: 16rem;
  }
  #header-container #page-links-nav-container li ul.sub-menu.showing a {
    color: #808080;
    justify-content: left;
    padding-left: 1rem;
  }
  #header-container #page-links-nav-container li ul.sub-menu.showing a:hover {
    color: #054877;
    background-color: #f2f2f2;
  }
  #header-container #page-links-nav-container li ul li {
    width: 100%;
  }
  #header-container #page-links-nav-container div[class$="french-container"] {
    font-size: 1.3rem;
  }
  #header-container #x_open {
    display: none;
  }
  #header-container .lsm-container {
    width: 290px;
    text-align: right;
    justify-content: center;
    padding-bottom: .3rem;
  }
  #header-container #page-links-nav-container div[class$="french-container"] {
    font-size: 1.54rem;
  }
  #header-container .lsm-container .portal-link.fr a {
    font-size: 1.54rem;
  }
  #header-container .lsm-container div[class$="language-switcher-fr-container"] {
    font-size: 1.54rem;
  }
}

@media (min-width: 1100px) {
  #header-container #page-links-nav-container div[class$="french-container"] {
    font-size: inherit;
  }
  #header-container .lsm-container .portal-link.fr a {
    font-size: inherit;
  }
  #header-container .lsm-container div[class$="language-switcher-fr-container"] {
    font-size: inherit;
  }
}
