#intro-text {
  display: flex;
  justify-content: center;
  margin-bottom: 4rem;
}

#intro-text div {
  padding: 1rem;
  margin: 1rem;
}

#intro-text div br {
  line-height: 5rem;
}

#main-features {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.main-feature-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.main-feature-image img {
  width: 10rem;
  height: 10rem;
}

.main-feature-title {
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: bold;
  margin: 1.5rem 0;
  color: #737373;
  text-align: center;
}

.main-feature-desc {
  text-align: center;
  width: 100%;
}

.tech-feature-desc-group {
  max-width: 100%;
}

@media (min-width: 300px) {
  .main-feature-wrapper {
    width: 30rem;
  }
}

@media (min-width: 400px) {
  .main-feature-wrapper {
    width: 40rem;
  }
}

@media (min-width: 1280px) {
  #intro-text div {
    max-width: 128rem;
  }
}
