#nrs-contact-us {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#nrs-contact-us i.fas {
  color: #00ACE6;
}

#nrs-contact-us .nrs-contact-us-elements {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 122rem;
  align-items: center;
}

#nrs-contact-us .nrs-contact-us-element {
  min-width: 30rem;
  flex: auto;
}

#nrs-contact-us .nrs-contact-us-cell {
  display: flex;
  padding: 1rem;
}

#nrs-contact-us .nrs-contact-title {
  text-align: center;
  font-size: 1.7rem;
  font-weight: bold;
  color: #00ACE6;
}

#nrs-contact-us .nrs-contact-details {
  display: flex;
  flex-direction: column;
}

#nrs-contact-us .nrs-contact-details .reminder {
  font-size: 1.5rem;
  line-height: 2rem;
}

@media (min-width: 300px) {
  #nrs-contact-us .nrs-contact-us-cell {
    flex-direction: column;
    margin-bottom: 1rem;
  }
  #nrs-contact-us .nrs-contact-title {
    width: 100%;
    text-align: center;
    margin-bottom: 1rem;
    line-height: 2.2rem;
  }
  #nrs-contact-us .nrs-contact-details {
    width: 100%;
  }
  #nrs-contact-us .nrs-contact-details span {
    text-align: center;
  }
  #nrs-contact-us .nrs-contact-us-element {
    margin-top: 1rem;
  }
}

@media (min-width: 600px) {
  #nrs-contact-us .nrs-contact-us-cell {
    flex-direction: row;
    margin-bottom: 0;
  }
  #nrs-contact-us .nrs-contact-title {
    width: 30%;
    text-align: right;
  }
  #nrs-contact-us .nrs-contact-details {
    width: 70%;
    padding-left: 1.3rem;
  }
  #nrs-contact-us .nrs-contact-details span {
    text-align: left;
  }
}

@media (min-width: 800px) {
  #nrs-contact-us .nrs-contact-us-elements {
    flex-direction: row;
  }
  #nrs-contact-us .nrs-contact-us-element img {
    margin-left: -40%;
  }
}
