#slider-container label {
  display: block;
}

#slider-container .iosSlider {
  width: 100%;
  height: 300px;
}

#slider-container .iosSlider .slider {
  width: 100%;
  height: 100%;
}

#slider-container .iosSlider .slider .item {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 300px;
  background: #fff;
  margin: 0 0 0 0;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

#slider-container .iosSlider .slider .item .text {
  position: absolute;
  bottom: 7rem;
  left: 10%;
  opacity: 0;
  padding: 0.5rem;
}

#slider-container .iosSlider .slider .item .text span {
  color: #fff;
}

#slider-container .iosSlider .slider #item1 {
  background-image: url(../../images/home-page-slides/slide-1.png);
}

#slider-container .iosSlider .slider #item2 {
  background-image: url(../../images/home-page-slides/slide-2.png);
}

#slider-container .iosSlider .slider #item3 {
  background-image: url(../../images/home-page-slides/slide-3.png);
}

#slider-container .iosSliderButtons {
  position: absolute;
  bottom: 10px;
  left: 10px;
  width: 200px;
  height: 10px;
}

#slider-container .iosSliderButtons .button {
  float: left;
  width: 9px;
  height: 9px;
  background: #999;
  margin: 0 10px 0 0;
  opacity: 0.25;
  border: 1px solid #000;
}

#slider-container .iosSliderButtons .selected {
  background: #000;
  opacity: 1;
}

@media screen and (min-width: 500px) {
  #slider-container .iosSlider .slider .item .text2 span {
    font-size: 3rem;
    line-height: 4.854rem;
  }
}

@media screen and (min-width: 1500px) {
  #slider-container .iosSlider .slider .item .text {
    left: 20%;
  }
}
