html {
  font-size: 62.5%;
}

body {
  font-size: 14px;
  font-size: 1.4rem;
}

/* =14px */
h1 {
  font-size: 24px;
  font-size: 2.4rem;
}

/* =24px */
h2 {
  font-size: 20px;
  font-size: 2rem;
}

body {
  background-color: #f2f2f2;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.8rem;
  line-height: 2.9124rem;
  background-color: #203864;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:active {
  text-decoration: underline;
}

h1 {
  font-size: 3rem;
}

input {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 300;
}

.wpml-ls-link {
  color: #fff;
}

#main-content-container {
  background-image: url(../../images/bkg/180618-1413.png);
}

#main-content-container .main-content-group {
  margin: 0 0 2rem;
}

#main-content-container .main-page-elements {
  padding: 1rem 0 4rem;
}

@media (min-width: 980px) {
  .wpml-ls-flag {
    border: 1px solid #666666;
  }
}
