#nrs_search {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 300px) {
  .news-post-search form div label:first-child {
    width: 1px;
  }
  .no-results {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .no-results-wrapper {
    width: 90%;
  }
}

@media (min-width: 1024px) {
  .no-results-wrapper {
    max-width: 1000px;
  }
}
