#tech-features {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 6%;
  padding-bottom: 6rem;
  margin: 0 1rem;
}

#tech-features .tech-feature-desc-group {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#tech-features .tech-feature-desc-group.three {
  justify-content: space-between;
}

#tech-features .tech-feature-desc-group.three .tech-feature-desc {
  width: 30%;
  display: flex;
  align-items: center;
}

#tech-features .tech-feature-desc-group .tech-feature-desc {
  width: 45%;
  text-align: center;
  background-image: radial-gradient(ellipse, white, transparent 70%);
  transition: all 0.5s ease;
  font-size: 1.5rem;
  line-height: 1.9rem;
}

#tech-features .tech-features-group-image {
  background-image: url(../../images/arrows.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

#tech-features .tech-features-group-image-wrapper {
  position: relative;
}

#tech-features .cog {
  transition: all 0.5s ease;
  position: absolute;
}

@media (min-width: 300px) {
  #tech-features .tech-features-group-image-wrapper {
    width: 30rem;
    height: 12rem;
  }
  #tech-features .tech-feature-desc-group.three .tech-feature-desc.left {
    text-align: center;
  }
  #tech-features .tech-feature-desc-group.three .tech-feature-desc.right {
    text-align: center;
  }
  #tech-features .tech-feature-desc-group .tech-feature-desc {
    max-width: 22rem;
  }
  .tech-features-group-image {
    padding: 2rem 0;
  }
  #cog1 {
    top: 3rem;
    left: 0%;
  }
  #cog1 img, #cog1 svg {
    width: 6rem;
    height: 6rem;
    transform: rotate(27deg);
  }
  #cog2 {
    top: 0rem;
    left: 17%;
  }
  #cog2 img, #cog2 svg {
    width: 7.0rem;
    height: 7.0rem;
    transform: rotate(10deg);
  }
  #cog3 {
    top: 1;
    left: 38%;
  }
  #cog3 img, #cog3 svg {
    width: 9.0rem;
    height: 9.0rem;
    transform: rotate(-22deg);
  }
  #cog4 {
    top: 5rem;
    left: 63%;
  }
  #cog4 img, #cog4 svg {
    width: 7.0rem;
    height: 7.0rem;
    transform: rotate(1deg);
  }
  #cog5 {
    top: 0.5rem;
    left: 78%;
  }
  #cog5 img, #cog5 svg {
    width: 6.5rem;
    height: 6.5rem;
    transform: rotate(8deg);
  }
}

@media (min-width: 500px) {
  #tech-features .tech-features-group-image-wrapper {
    width: 40rem;
    height: 16rem;
  }
  .tech-features-group-image {
    padding: 3rem 0;
  }
  #cog1 {
    top: 4rem;
    left: 0%;
  }
  #cog1 img, #cog1 svg {
    width: 8rem;
    height: 8rem;
  }
  #cog2 {
    top: 0rem;
    left: 17%;
  }
  #cog2 img, #cog2 svg {
    width: 9.33333rem;
    height: 9.33333rem;
  }
  #cog3 {
    top: 1;
    left: 38%;
  }
  #cog3 img, #cog3 svg {
    width: 12rem;
    height: 12rem;
  }
  #cog4 {
    top: 6.66667rem;
    left: 63%;
  }
  #cog4 img, #cog4 svg {
    width: 9.33333rem;
    height: 9.33333rem;
  }
  #cog5 {
    top: 0.66667rem;
    left: 78%;
  }
  #cog5 img, #cog5 svg {
    width: 8.66667rem;
    height: 8.66667rem;
  }
}

@media screen and (min-width: 600px) {
  #tech-features .tech-feature-desc-group .tech-feature-desc {
    font-size: 1.8rem;
    line-height: 2.3rem;
  }
  #tech-features .tech-feature-desc-group.three .tech-feature-desc.left {
    text-align: right;
  }
  #tech-features .tech-feature-desc-group.three .tech-feature-desc.right {
    text-align: left;
  }
}
